@import '../../node_modules/sweetalert2/dist/sweetalert2.min.css';
body {
  background-color: #f5f5f5;
}
#root .header {
  width: 100%;
  height: 80px;
  background-color: rgba(245,245,245,0.4);
}
#root .header .left {
  display: inline-block;
  height: 100%;
  padding: 20px 32px;
}
#root .header .left .logo {
  width: 40px;
  height: 40px;
  vertical-align: middle;
}
#root .header .left .title {
  font-size: 18px;
  font-weight: 700;
  padding-left: 12px;
  line-height: 40px;
}
#root .header .tab {
  display: inline-block;
  padding-left: 64px;
  height: 100%;
  vertical-align: middle;
}
#root .header .tab .tab-item {
  display: inherit;
  height: 100%;
  vertical-align: middle;
  padding-right: 32px;
}
#root .header .tab .tab-item:last-of-type {
  padding-right: 0;
}
#root .header .tab .tab-item .item {
  font-size: 16px;
  line-height: 80px;
}
#root .header .tab .tab-item .item:hover {
  color: #fa3140;
}
#root .header .form-wrapper {
  float: right;
  height: 100%;
  padding-right: 72px;
}
#root .header .form-wrapper .form-item {
  padding-top: 20px;
}
#root .header .form-wrapper .form-item .form-control {
  height: 100%;
}
#root .header .form-wrapper .form-item .form-control .input-content {
  border: 0;
  padding: 12px 0 12px 6px;
  border-bottom: 1px solid rgba(7,17,27,0.2);
  background-color: transparent;
}
#root .header .form-wrapper .form-item .form-control .submit {
  display: inline-block;
  border: 0;
  padding-top: 12px;
  background-color: transparent;
}
#root .header .form-wrapper .form-item .form-control .submit .iconfont {
  color: #999;
  font-size: 16px;
}
#root .header .right {
  float: right;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
#root .header .right .shopcar {
  flex: 0 0 100px;
  width: 100px;
  cursor: pointer;
}
#root .header .right .shopcar:hover {
  color: #fa3140;
}
#root .header .right .shopcar:hover > .title {
  color: #fa3140;
}
#root .header .right .shopcar .iconfont {
  font-size: 24px;
  padding-right: 12px;
}
#root .header .right .shopcar .title {
  color: #999;
}
#root .header .right .sign {
  flex: 1;
  padding-left: 24px;
  color: #999;
}
#root .header .right .sign .signin {
  display: inline-block;
  padding-right: 32px;
}
#root .header .right .sign .signin:hover {
  color: #fa3140;
  cursor: pointer;
}
#root .header .right .sign .signup {
  display: inline-block;
  padding-right: 32px;
}
#root .header .right .sign .signup:hover {
  color: #fa3140;
  cursor: pointer;
}
#root .nav {
  width: 100%;
  min-height: 460px;
  text-align: center;
  background-image: url("/static/images/bk.jpg");
  background-size: 100% 400px;
  background-repeat: no-repeat;
}
#root .nav .nav-wrapper {
  display: inline-block;
  width: 1200px;
  margin-top: 48px;
}
#root .nav .nav-wrapper .content-wrapper {
  display: flex;
  flex-flow: row nowrap;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
#root .nav .nav-wrapper .content-wrapper .left-tab {
  flex: 0 0 200px;
  padding: 12px;
  background-color: rgba(147,153,159,0.1);
  cursor: pointer;
}
#root .nav .nav-wrapper .content-wrapper .left-tab .tab {
  width: 100%;
}
#root .nav .nav-wrapper .content-wrapper .left-tab .tab .item {
  text-align: left;
  text-indent: 12px;
  padding: 24px 0;
  color: #fff;
  border-bottom: 1px solid rgba(255,255,255,0.5);
}
#root .nav .nav-wrapper .content-wrapper .left-tab .tab .item:last-of-type {
  border-bottom: 0;
}
#root .nav .nav-wrapper .content-wrapper .left-tab .tab .item:hover {
  background-color: #93999f;
}
#root .nav .nav-wrapper .content-wrapper .left-tab .tab .item .title {
  font-size: 16px;
  font-weight: 500;
}
#root .nav .nav-wrapper .content-wrapper .left-tab .tab .item .goto {
  float: right;
  padding-right: 24px;
}
#root .nav .nav-wrapper .content-wrapper .right-bander {
  flex: 1;
  position: relative;
}
#root .nav .nav-wrapper .content-wrapper .right-bander .toleft,
#root .nav .nav-wrapper .content-wrapper .right-bander .toright {
  display: inline-block;
  position: relative;
  width: 100px;
  height: 100%;
}
#root .nav .nav-wrapper .content-wrapper .right-bander .toleft:hover,
#root .nav .nav-wrapper .content-wrapper .right-bander .toright:hover {
  background-color: rgba(147,153,159,0.6);
}
#root .nav .nav-wrapper .content-wrapper .right-bander .toleft .iconfont,
#root .nav .nav-wrapper .content-wrapper .right-bander .toright .iconfont {
  color: #fff;
  font-size: 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}
#root .nav .nav-wrapper .content-wrapper .right-bander .toleft {
  float: left;
}
#root .nav .nav-wrapper .content-wrapper .right-bander .toright {
  float: right;
}
#root .nav .nav-wrapper .content-wrapper .right-bander .check-point {
  position: absolute;
  padding: 12px;
  bottom: 32px;
  left: 50%;
  transform: translate3d(-50%, 0, 0);
}
#root .nav .nav-wrapper .content-wrapper .right-bander .check-point .point {
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 16px;
  background-color: rgba(147,153,159,0.8);
  margin-right: 6px;
}
#root .nav .nav-wrapper .content-wrapper .right-bander .check-point .point:last-of-type {
  margin-right: 0;
}
#root .nav .nav-wrapper .content-wrapper .right-bander .check-point .point.active {
  background-color: #fa3140;
  opacity: 0.8;
}
#root .nav .nav-wrapper .nav-bottom {
  min-height: 120px;
  background-color: #fff;
  box-shadow: 0 5px 10px #ddd, 0 2px 10px #ddd inset;
}
#root .nav .nav-wrapper .nav-bottom .bottom-tab {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
}
#root .nav .nav-wrapper .nav-bottom .bottom-tab .item {
  flex: 0 0 20%;
  height: 120px;
  background-size: 100%;
  background-repeat: no-repeat;
  cursor: pointer;
  background-position: center -10%;
  transition: transform 0.3s;
}
#root .nav .nav-wrapper .nav-bottom .bottom-tab .item:hover {
  transform: translate3d(0, -12px, 0);
}
#root .wrapper {
  text-align: center;
}
#root .wrapper .details-wrapper {
  width: 1200px;
  margin: 0 auto;
}
#root .wrapper .details-wrapper .title {
  padding: 24px 0;
  text-align: left;
  font-size: 20px;
  font-weight: 700;
}
#root .wrapper .details-wrapper .title .more {
  float: right;
  font-size: 14px;
  color: #999;
  line-height: 24px;
}
#root .wrapper .details-wrapper .title .more .iconfont {
  font-size: 12px;
  color: #999;
  line-height: 24px;
}
#root .wrapper .details-wrapper .details {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
}
#root .wrapper .details-wrapper .details .detail {
  flex: 0 0 20%;
  width: 20%;
  padding-right: 12px;
  box-sizing: border-box;
  padding-bottom: 24px;
  cursor: pointer;
}
#root .wrapper .details-wrapper .details .detail:hover > .body {
  box-shadow: 2px 2px 10px #ddd, 2px 0 10px #ddd inset;
}
#root .wrapper .details-wrapper .details .detail:nth-of-type(5n) {
  padding-right: 0;
}
#root .wrapper .details-wrapper .details .detail .child-title {
  padding: 12px;
  height: 32px;
  line-height: 32px;
  text-align: left;
  color: #fff;
  background: linear-gradient(270deg, rgba(210,115,230,0.7), #d273e6);
}
#root .wrapper .details-wrapper .details .detail .body {
  height: 200px;
  padding: 12px;
  box-sizing: border-box;
  background-color: #fff;
  transition: box-shadow 0.3s;
  box-shadow: 2px 2px 2px #ddd, 2px 0 5px #ddd inset;
}
#root .wrapper .details-wrapper .details .detail .body .heading {
  font-weight: 500;
  font-size: 14px;
}
